import { unwrapResult } from "@reduxjs/toolkit";
import { Button, Card, Form, Input, notification } from "antd";
import React from "react";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import { ChangePassword } from "../ChangePasswordSlice";

const ChangePasswordPage = () => {
  const dispatch = useDispatch();
  const history = useHistory();

  const onFinish = async (data) => {
    try {
      const resultAction = await dispatch(ChangePassword(data));
      unwrapResult(resultAction);
      notification.success({ message: "Cp nht ti khon thnh cng" });
      history.push("/pt");
    } catch (error) {
      notification.error({ message: error });
    }
  };

  return (
    <Card title="Thay i mt khu" className="tw-bg-gray-100">
      <Form
        labelCol={{
          span: 8,
          className: "tw-text-left",
        }}
        onFinish={onFinish}
      >
        <Form.Item
          name="old_password"
          label="Mt khu c"
          rules={[{ required: true, message: "Nhp mt khu c" }]}
        >
          <Input.Password size="large" />
        </Form.Item>
        <Form.Item
          name="new_password"
          label="Mt khu mi"
          rules={[{ required: true, message: "Hy nhp mt khu mi" }]}
        >
          <Input.Password size="large" />
        </Form.Item>
        <Form.Item
          name="cf_password"
          label="Nhp li mt khu mi"
          rules={[{ required: true, message: "Hy nhp li mt khu mi" }]}
        >
          <Input.Password size="large" />
        </Form.Item>
        <div className="tw-flex tw-justify-end">
          <Form.Item>
            <Button type="primary" htmlType="submit">
              i mt khu
            </Button>
            <Button
              className="ml-4"
              type="primary"
              danger
              onClick={() => history.goBack()}
            >
              Quay li
            </Button>
          </Form.Item>
        </div>
      </Form>
    </Card>
  );
};

export default ChangePasswordPage;
