import { unwrapResult } from "@reduxjs/toolkit";
import { Col, Form, Input, notification, Row, Switch } from "antd";
import React, { useEffect } from "react";
import { useDispatch } from "react-redux";
import FormModal from "../../../../components/componentsAdmin/Modal/FormModal";
import { addSpecialize, updateSpecialize } from "../SpecializeSlice";

const SpecializeForm = (props) => {
  const { handleCancel, openModal, dataEdit, type } = props;
  const [form] = Form.useForm();
  const dispatch = useDispatch();
  useEffect(() => {
    if (dataEdit !== null) {
      form.setFieldsValue({
        name: dataEdit?.name,
        status: dataEdit?.status === "Active" ? true : false,
        description: dataEdit?.description,
      });
    } else {
      form.resetFields();
    }
  }, [dataEdit, form]);
  const onFinish = async (value) => {
    const payload = {
      name: value?.name,
      status: value?.status === true ? "Active" : "Inactive",
      description: value?.description,
    };
    if (type === "update" && dataEdit?.id) {
      try {
        const id = dataEdit?.id;
        const resulDispatch = await dispatch(updateSpecialize({ id, payload }));
        if (resulDispatch.error) {
          form.setFields([
            resulDispatch.payload.name
              ? {
                  name: "name",
                  errors: [resulDispatch.payload.name],
                }
              : "",
            resulDispatch.payload.description
              ? {
                  name: "description",
                  errors: [resulDispatch.payload.description],
                }
              : "",
          ]);
        }
        unwrapResult(resulDispatch);
        notification.success({ message: `Sa chuyn mn thnh cng !!!` });
        handleCancel();
        form.resetFields();
      } catch (error) {
        return notification.error({ message: `Sa chuyn mn tht bi ! ` });
      }
    } else {
      try {
        const resulDispatch = await dispatch(addSpecialize(payload));
        if (resulDispatch.error) {
          form.setFields([
            resulDispatch.payload.name
              ? {
                  name: "name",
                  errors: [resulDispatch.payload.name],
                }
              : "",
            resulDispatch.payload.description
              ? {
                  name: "description",
                  errors: [resulDispatch.payload.description],
                }
              : "",
          ]);
        }
        unwrapResult(resulDispatch);
        notification.success({ message: `Thm chuyn mn thnh cng !` });
        handleCancel();
        form.resetFields();
      } catch (error) {
        return notification.error({ message: `Thm chuyn mn tht bi ! ` });
      }
    }
  };
  return (
    <>
      <FormModal
        titleModal={type === "create" ? "Thm mi" : `Cp nht`}
        titleForm={"specialize"}
        visible={openModal}
        handleCancel={() => {
          handleCancel();
          form.resetFields();
        }}
      >
        <Form layout="vertical" form={form} id="specialize" onFinish={onFinish}>
          <Row gutter={24}>
            <Col span={24}>
              <Row gutter={24}>
                <Col span={12}>
                  <Form.Item
                    label="Tn chuyn mn"
                    name="name"
                    rules={[
                      {
                        required: true,
                        message: "Mi nhp tn chuyn mn",
                      },
                    ]}
                  >
                    <Input placeholder="Hy nhp tn chuyn mn..." />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item label="Trng thi" name="status">
                    <Switch />
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item
                    label="M t"
                    name="description"
                    rules={[
                      {
                        required: true,
                        message: "Mi nhp m t",
                      },
                    ]}
                  >
                    <Input.TextArea />
                  </Form.Item>
                </Col>
              </Row>
            </Col>
          </Row>
        </Form>
      </FormModal>
    </>
  );
};

export default SpecializeForm;
