import { unwrapResult } from "@reduxjs/toolkit";
import { UploadOutlined } from "@ant-design/icons";
import {
  Col,
  Form,
  Input,
  notification,
  Row,
  Select,
  Upload,
  Image,
  message,
  Switch,
  Button,
} from "antd";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import FormModal from "../../../../components/componentsAdmin/Modal/FormModal";
import { getAllSpecialize } from "../../../Admin/Specialize/SpecializeSlice";
import { getInfoUser } from "../../../UserSlice";
import user from "../../../../assets/user.png";
import { uploadImg } from "../../../../utils/uploadImg";
import { updateAccountLevel, addAccountLevel } from "./../AccountLevelSlice";

const { Option } = Select;
const AccountLevelForm = (props) => {
  const dispatch = useDispatch();
  const [imageUrl, setImageUrl] = useState("");
  const { handleCancel, openModal, dataEdit, type } = props;
  const [form] = Form.useForm();
  // const listAllAccountLevel = useSelector((state) => state.AccountLevel.items);
  const uploadImageHandle = {
    async onChange(info) {
      const typeImg = info?.file?.type;
      if (typeImg === "image/png" || typeImg === "image/jpeg") {
        await uploadImg(info.file).then((img) => {
          setImageUrl(img);
        });
        message.success("Ti nh thnh cng !");
      } else {
        message.error("Vui lng chn ng nh dng nh !");
      }
    },
  };

  useEffect(() => {
    if (dataEdit !== null) {
      form.setFieldsValue({
        name: dataEdit?.name,
        image: dataEdit?.image,
        display_name: dataEdit?.display_name,
        course_number: dataEdit?.course_number,
        user_number: dataEdit?.user_number,
        status: dataEdit?.status,
        is_mutable: dataEdit?.is_mutable,
      });
      setImageUrl(dataEdit?.image);
    } else {
      form.resetFields();
      setImageUrl("");
    }
  }, [dataEdit, form]);
  const onFinish = async (value) => {
    const payload = {
      name: value?.name,
      image: imageUrl,
      display_name: value?.display_name,
      course_number: value?.course_number,
      user_number: value?.user_number,
      status: value?.status === true ? "Active" : "Inactive",
      is_mutable: value?.is_mutable === true ? 1 : 2,
    };

    if (type === "update" && dataEdit?.id) {
      try {
        const id = dataEdit?.id;
        const resulDispatch = await dispatch(updateAccountLevel({ id, payload }));
        if (resulDispatch.error) {
          form.setFields([
            resulDispatch.payload.name
              ? {
                  name: "name",
                  errors: [resulDispatch.payload.name],
                }
              : "",
            resulDispatch.payload.display_name
              ? {
                  name: "display_name",
                  errors: [resulDispatch.payload.display_name],
                }
              : "",
            resulDispatch.payload.course_number
              ? {
                  name: "course_number",
                  errors: [resulDispatch.payload.course_number],
                }
              : "",
            resulDispatch.payload.user_number
              ? {
                  name: "user_number",
                  errors: [resulDispatch.payload.user_number],
                }
              : "",
            resulDispatch.payload.status
              ? {
                  name: "status",
                  errors: [resulDispatch.payload.status],
                }
              : "",
          ]);
        }
        unwrapResult(resulDispatch);
        notification.success({ message: `Sa chc v thnh cng !!!` });
        handleCancel();
        form.resetFields();
      } catch (error) {
        notification.error({ message: `Sa chc v tht bi !` });
      }
    } else {
      try {
        const resulDispatch = await dispatch(addAccountLevel(payload));
        if (resulDispatch.error) {
          form.setFields([
            resulDispatch.payload.name
              ? {
                  name: "name",
                  errors: [resulDispatch.payload.name],
                }
              : "",
            resulDispatch.payload.display_name
              ? {
                  name: "display_name",
                  errors: [resulDispatch.payload.display_name],
                }
              : "",
            resulDispatch.payload.course_number
              ? {
                  name: "course_number",
                  errors: [resulDispatch.payload.course_number],
                }
              : "",
            resulDispatch.payload.user_number
              ? {
                  name: "user_number",
                  errors: [resulDispatch.payload.user_number],
                }
              : "",
            resulDispatch.payload.status
              ? {
                  name: "status",
                  errors: [resulDispatch.payload.status],
                }
              : "",
          ]);
        }
        unwrapResult(resulDispatch);
        notification.success({ message: `Thm chc v thnh cng !!!` });
        handleCancel();
        form.resetFields();
      } catch (error) {
        notification.error({ message: `Thm chc v tht bi !` });
      }
    }
  };
  return (
    <>
      <FormModal
        titleModal={type === "create" ? "Thm mi" : `Cp nht`}
        titleForm={"specializedetailpt"}
        visible={openModal}
        handleCancel={() => {
          handleCancel();
          form.resetFields();
        }}
        width={"60%"}
      >
        <Form layout="vertical" form={form} id="specializedetailpt" onFinish={onFinish}>
          <Row gutter={24}>
            <Col span={24} lg={{ span: 16 }}>
              <Row gutter={16}>
                <Col span={24} lg={{ span: 12 }}>
                  <Form.Item
                    label="Tn cp "
                    name="name"
                    rules={[
                      {
                        required: true,
                        message: "Mi nhp tn cp ",
                      },
                    ]}
                  >
                    <Input placeholder="Tn cp " />
                  </Form.Item>
                </Col>
                <Col span={24} lg={{ span: 12 }}>
                  <Form.Item
                    label="Tn hin th"
                    name="display_name"
                    rules={[
                      {
                        required: true,
                        message: "Mi nhp tn hin th",
                      },
                    ]}
                  >
                    <Input placeholder="Tn hin th" />
                  </Form.Item>
                </Col>
                <Col span={24} lg={{ span: 12 }}>
                  <Form.Item
                    label="S kho hc ti thiu"
                    name="course_number"
                    rules={[
                      {
                        required: true,
                        message: "Mi nhp kho hc ti thiu",
                      },
                      {
                        pattern: /^(0|[1-9][0-9]*)$/,
                        message: "Vui lng nhp bng s",
                      },
                    ]}
                  >
                    <Input placeholder="Tn kho hc ti thiu" />
                  </Form.Item>
                </Col>
                <Col span={24} lg={{ span: 12 }}>
                  <Form.Item
                    label="S hc vin ti thiu"
                    name="user_number"
                    rules={[
                      {
                        required: true,
                        message: "Mi nhp hc vin ti thiu",
                      },
                      {
                        pattern: /^(0|[1-9][0-9]*)$/,
                        message: "Vui lng nhp bng s",
                      },
                    ]}
                  >
                    <Input placeholder="Tn hc vin ti thiu" />
                  </Form.Item>
                </Col>
                <Col span={24} lg={{ span: 12 }}>
                  <Form.Item label="Trng thi" name="status" valuePropName="checked">
                    <Switch
                      {...(dataEdit?.status === "Active" ? { checked: true } : { checked: false })}
                    />
                  </Form.Item>
                </Col>
                <Col span={24} lg={{ span: 12 }}>
                  <Form.Item label="Quyn xo" name="status" valuePropName="checked">
                    <Switch
                      {...(dataEdit?.is_mutable === 1 ? { checked: true } : { checked: false })}
                    />
                  </Form.Item>
                </Col>
              </Row>
            </Col>
            <Col span={24} lg={{ span: 8 }}>
              <Row style={{ justifyContent: "center" }}>
                <Form.Item name="image">
                  <Image
                    src={imageUrl !== "" ? imageUrl : user}
                    style={{
                      height: 150,
                      width: 150,
                      borderRadius: "50%",
                      marginTop: "27px",
                    }}
                    alt="Hnh nh khng tn ti"
                  />
                </Form.Item>
              </Row>
              <Row style={{ justifyContent: "center" }}>
                <Upload beforeUpload="false" showUploadList={false} {...uploadImageHandle}>
                  <Button icon={<UploadOutlined />}>
                    {type === "update" ? "Thay i nh" : "Chn nh"}
                  </Button>
                </Upload>
              </Row>
            </Col>
          </Row>
        </Form>
      </FormModal>
    </>
  );
};

export default AccountLevelForm;
