import CIcon from "@coreui/icons-react";
import {
  CCreateElement,
  CSidebar,
  CSidebarBrand,
  CSidebarMinimizer,
  CSidebarNav,
  CSidebarNavDivider,
  CSidebarNavDropdown,
  CSidebarNavItem,
  CSidebarNavTitle,
} from "@coreui/react";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import logo2 from "../../../assets/favicon.png";
import logo from "../../../assets/images/clinet/logo-2.png";
// sidebar nav config
import navigation from "./_nav";
import ROUTER from "../../../router/router";

const TheSidebar = () => {
  const permissionUser = useSelector(
    (state) => state?.InfoUser?.infoUser?.array_permissions
  );
  if (permissionUser) {
    const TaiKhoan = permissionUser?.includes("user:list");
    const Quyen = permissionUser?.includes("permission:list");
    const ChucVu = permissionUser?.includes("role:list");
    const DSKhoahoc = permissionUser?.includes("course:list");
    const HocVienDK = permissionUser?.includes("student:list");
    const yeuCauThanhToan = permissionUser?.includes("payment:list");
    const cauhinhWeb = permissionUser?.includes("setting:list");
    const chuyenMon = permissionUser?.includes("specialize:list");
    const capDoPT = permissionUser?.includes("account-level:list");
    const hoaDonKH = permissionUser?.includes("bill:customer");
    const hoaDonPT = permissionUser?.includes("bill:payment");
    const hoaDonGD = permissionUser?.includes("bill:vnp");
    const lienHe = permissionUser?.includes("contact:list");
    const khieuNai = permissionUser?.includes("complain:list");
    const danhGiaKhoaHoc = permissionUser?.includes("comment:list");
    //
    if (TaiKhoan || Quyen || ChucVu) {
      navigation.push({
        _tag: "CSidebarNavTitle",
        _children: ["Ti khon"],
      });
    }
    if (TaiKhoan) {
      navigation.push({
        _tag: "CSidebarNavItem",
        name: "Qun l ti khon",
        to: ROUTER.ADMIN.USER,
        icon: "cil-user",
      });
    }
    if (Quyen) {
      navigation.push({
        _tag: "CSidebarNavItem",
        name: "Qun l quyn",
        to: ROUTER.ADMIN.PERMISSION,
        icon: "cil-fork",
      });
    }
    if (ChucVu) {
      navigation.push({
        _tag: "CSidebarNavItem",
        name: "Qun l chc v",
        to: ROUTER.ADMIN.ROLE,
        icon: "cil-braille",
      });
    }

    //
    if (DSKhoahoc || HocVienDK || yeuCauThanhToan) {
      navigation.push({
        _tag: "CSidebarNavTitle",
        _children: ["Kho hc"],
      });
    }
    if (DSKhoahoc) {
      navigation.push({
        _tag: "CSidebarNavItem",
        name: "Danh sch kho hc",
        to: ROUTER.ADMIN.COURSE_REQUEST,
        icon: "cil-pencil",
      });
    }
    if (HocVienDK) {
      navigation.push({
        _tag: "CSidebarNavItem",
        name: "Hc vin  ng k",
        to: ROUTER.ADMIN.STUDENT_REGISTERED,
        icon: "cil-user",
      });
    }
    if (yeuCauThanhToan) {
      navigation.push({
        _tag: "CSidebarNavItem",
        name: "Yu cu thanh ton PT",
        to: ROUTER.ADMIN.BILL_PAY_FOR_PT,
        icon: "cil-basket",
      });
    }

    //
    if (cauhinhWeb) {
      navigation.push({
        _tag: "CSidebarNavTitle",
        _children: ["Cu hnh Website"],
      });
    }
    if (cauhinhWeb) {
      navigation.push({
        _tag: "CSidebarNavItem",
        name: "Qun l cu hnh",
        icon: "cil-settings",
        to: ROUTER.ADMIN.SETTING,
      });
    }

    //
    if (chuyenMon) {
      navigation.push({
        _tag: "CSidebarNavTitle",
        _children: ["Chuyn mn"],
      });
    }
    if (chuyenMon) {
      navigation.push({
        _tag: "CSidebarNavItem",
        name: "Qun l chuyn mn",
        icon: "cil-star",
        to: ROUTER.ADMIN.SPECIALIZE,
      });
    }

    //
    if (capDoPT) {
      navigation.push({
        _tag: "CSidebarNavTitle",
        _children: ["Cp "],
      });
    }
    if (capDoPT) {
      navigation.push({
        _tag: "CSidebarNavItem",
        name: "Qun l cp  PT",
        to: ROUTER.ADMIN.ACCOUNTLEVEL,
        icon: "cil-shield-alt",
      });
    }

    //
    if (hoaDonKH || hoaDonPT || hoaDonGD) {
      navigation.push({
        _tag: "CSidebarNavTitle",
        _children: ["Ha n"],
      });
    }
    if (hoaDonKH) {
      navigation.push({
        _tag: "CSidebarNavItem",
        name: "Ha n khch hng",
        to: ROUTER.ADMIN.BILLINGS,
        icon: "cil-credit-card",
      });
    }
    if (hoaDonPT) {
      navigation.push({
        _tag: "CSidebarNavItem",
        name: "Ha n thanh ton PT",
        to: ROUTER.ADMIN.BILL_COURSES_PT,
        icon: "cil-credit-card",
      });
    }
    if (hoaDonGD) {
      navigation.push({
        _tag: "CSidebarNavItem",
        name: "Qun l giao dch",
        to: ROUTER.ADMIN.TRANSACTIONS,
        icon: "cil-basket",
      });
    }

    //
    if (khieuNai || danhGiaKhoaHoc) {
      navigation.push({
        _tag: "CSidebarNavTitle",
        _children: ["Khiu ni"],
      });
    }
    if (khieuNai) {
      navigation.push({
        _tag: "CSidebarNavItem",
        name: "Qun l khiu ni",
        to: ROUTER.ADMIN.COMPLAIN,
        icon: "cilFrown",
      });
    }
    if (danhGiaKhoaHoc) {
      navigation.push({
        _tag: "CSidebarNavItem",
        name: "Qun l nh gi kha hc",
        to: ROUTER.ADMIN.MANAGE_REVIEWS,
        icon: "cilCommentSquare",
      });
    }

    //
    if (lienHe) {
      navigation.push({
        _tag: "CSidebarNavTitle",
        _children: ["Lin h"],
      });
    }
    if (lienHe) {
      navigation.push({
        _tag: "CSidebarNavItem",
        name: "Lin h",
        to: ROUTER.ADMIN.CONTACT,
        icon: "cilCommentSquare",
      });
    }
  }

  const dispatch = useDispatch();
  const show = useSelector((state) => state.changeState.sidebarShow);
  return (
    <CSidebar
      show={show}
      onShowChange={(val) => dispatch({ type: "set", sidebarShow: val })}
    >
      <CSidebarBrand className="d-md-down-none" to="/">
        <CIcon
          src={logo}
          className="c-sidebar-brand-full"
          name="logo-negative"
          height={35}
        />
        <CIcon
          src={logo2}
          className="c-sidebar-brand-minimized"
          name="sygnet"
          height={35}
        />
      </CSidebarBrand>
      <CSidebarNav>
        <CCreateElement
          items={navigation}
          components={{
            CSidebarNavDivider,
            CSidebarNavDropdown,
            CSidebarNavItem,
            CSidebarNavTitle,
          }}
        />
      </CSidebarNav>
      <CSidebarMinimizer className="c-d-md-down-none" />
    </CSidebar>
  );
};

export default React.memo(TheSidebar);
