import { LoadingOutlined, PlusOutlined } from "@ant-design/icons";
import { unwrapResult } from "@reduxjs/toolkit";
import { Col, Form, Input, message, notification, Row, Upload } from "antd";
import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useParams } from "react-router-dom";
import FormModal from "../../../../components/componentsAdmin/Modal/FormModal";
import { UPLOAD_API_URL } from "../../../../utils/Config";
import {
  addCertificatesBySpecialize,
  getCertificatesBySpecialize,
  updateCertificatesBySpecialize,
} from "../SpecializeDetailPtSilce";

const CertificatesBySpecializeDetailForm = (props) => {
  const { id } = useParams();
  const dispatch = useDispatch();
  const { handleCancel, openModal, dataEdit, type } = props;
  const [isUploading, setIsUploading] = useState(false);
  const [imageUrl, setImageUrl] = useState("");
  const [form] = Form.useForm();
  useEffect(() => {
    if (dataEdit !== null) {
      form.setFieldsValue({
        name: dataEdit?.name,
        image: dataEdit?.image,
      });
      setImageUrl(dataEdit?.image);
    } else {
      form.resetFields();
    }
    dispatch(getCertificatesBySpecialize(id));
  }, [dataEdit, form]);

  function getBase64(img, callback) {
    const reader = new FileReader();
    reader.addEventListener("load", () => callback(reader.result));
    reader.readAsDataURL(img);
  }
  function beforeUpload(file) {
    const isJpgOrPng = file.type === "image/jpeg" || file.type === "image/png";
    if (!isJpgOrPng) {
      message.error("Ch c th ti ln file JPEG v PNG!");
    }
    const isLt2M = file.size / 1024 / 1024 < 5;
    if (!isLt2M) {
      message.error("nh phi nh hn 5MB!");
    }
    return isJpgOrPng && isLt2M;
  }
  const handleUploadImage = (info) => {
    if (info.file.status === "uploading") {
      setIsUploading(true);
      return;
    }
    if (info.file.status === "done") {
      getBase64(info.file.originFileObj, (imageUrl) => {
        setImageUrl(info.file.response.url);
        setIsUploading(false);
      });
      message.success(`Ti ln nh ${info.file.originFileObj.name} thnh cng`);
    }
    if (info.file.status === "error") {
      setIsUploading(false);
      message.error(`${info.file.response.status}`);
    }
  };
  const uploadButton = (
    <div>
      {isUploading ? <LoadingOutlined /> : <PlusOutlined />}
      <div style={{ marginTop: 8 }}>Ti ln</div>
    </div>
  );

  const onFinish = async (value) => {
    const payload = {
      name: value?.name,
      image: imageUrl ? imageUrl : dataEdit?.image,
      specialize_detail_id: id,
    };
    if (type === "update" && dataEdit?.id) {
      try {
        const id = dataEdit?.id;
        const resultAction = await dispatch(updateCertificatesBySpecialize({ id, payload }));
        if (resultAction.error) {
          form.setFields([
            resultAction.payload.name
              ? {
                  name: "name",
                  errors: [resultAction.payload.name],
                }
              : "",
            resultAction.payload.image
              ? {
                  name: "image",
                  errors: [resultAction.payload.image],
                }
              : "",
          ]);
        }
        unwrapResult(resultAction);
        notification.success({ message: `Sa chng ch thnh cng !!!` });
        handleCancel();
        form.resetFields();
        setImageUrl("");
      } catch (error) {
        notification.error({ message: `Sa chng ch tht bi !` });
      }
    } else {
      try {
        const resulDispatch = await dispatch(addCertificatesBySpecialize(payload));
        if (resulDispatch.error) {
          form.setFields([
            resulDispatch.payload.name
              ? {
                  name: "name",
                  errors: [resulDispatch.payload.name],
                }
              : "",
            resulDispatch.payload.image
              ? {
                  name: "image",
                  errors: [resulDispatch.payload.image],
                }
              : "",
          ]);
        }
        unwrapResult(resulDispatch);
        notification.success({ message: `Thm chng ch thnh cng !!!` });
        handleCancel();
        form.resetFields();
      } catch (error) {
        notification.error({ message: `Thm chng ch tht bi !` });
      }
    }
  };
  return (
    <>
      <FormModal
        titleModal={type === "create" ? "Thm mi" : `Cp nht`}
        titleForm={"certificatesbyspec"}
        visible={openModal}
        handleCancel={() => {
          handleCancel();
          form.resetFields();
          setImageUrl("");
        }}
      >
        <Form layout="vertical" form={form} id="certificatesbyspec" onFinish={onFinish}>
          <Row gutter={24}>
            <Col span={24}>
              <Row gutter={24}>
                <Col span={4} />
                <Col span={15}>
                  <Form.Item
                    label="Tn chng ch"
                    name="name"
                    rules={[
                      {
                        required: true,
                        message: "Mi nhp tn chng ch",
                      },
                    ]}
                  >
                    <Input placeholder="Hy nhp tn chng ch..." />
                  </Form.Item>
                </Col>
                <Col span={4} />
                <Col span={4} />
                <Col span={8}>
                  <Form.Item
                    label="nh"
                    name="image"
                    rules={[
                      {
                        required: true,
                        message: "Mi chn nh",
                      },
                    ]}
                  >
                    <Upload
                      listType="picture-card"
                      className="avatar-uploader"
                      showUploadList={false}
                      action={UPLOAD_API_URL}
                      beforeUpload={beforeUpload}
                      onChange={handleUploadImage}
                    >
                      {imageUrl ? (
                        <img
                          src={imageUrl}
                          alt="avatar"
                          style={{ width: "100%", height: "100%" }}
                        />
                      ) : (
                        uploadButton
                      )}
                    </Upload>
                  </Form.Item>
                </Col>
                {/* <Col span={4}>
                  <Form.Item>{renderImgEdit(dataEdit?.image)}</Form.Item>
                </Col> */}
              </Row>
            </Col>
          </Row>
        </Form>
      </FormModal>
    </>
  );
};

export default CertificatesBySpecializeDetailForm;
