import {
  Avatar,
  Card,
  Form,
  Input,
  notification,
  Rate,
  List,
  Empty,
} from "antd";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { LoadingOutlined } from "@ant-design/icons";
import { addComment } from "../CourseDetailSlice";
import { unwrapResult } from "@reduxjs/toolkit";
const CourseComment = () => {
  const dispatch = useDispatch();
  const [form] = Form.useForm();
  const { courseDetail, loadingAddComment, comments_client } = useSelector(
    (state) => state.CourseDetail
  );
  const { infoUser } = useSelector((state) => state.InfoUser);
  const onFinish = async (data) => {
    try {
      const newData = {
        ...data,
        id_course: courseDetail?.detail_course?.id,
        user_id: infoUser?.id,
      };
      const resulDispatch = await dispatch(addComment(newData));
      unwrapResult(resulDispatch);
      notification.success({
        message: "nh gi kha hc thnh cng",
        description:
          "Cm n bn  nh gi kha hc. nh gi ca bn rt hu ch cho nhng hc vin ang quan tm kha hc ny",
      });
      form.resetFields();
    } catch (error) {
      if (typeof error !== "object") {
        notification.error({
          message: error,
        });
      }
    }
  };
  return (
    <Card
      id="course-comment"
      title="Nhn xt ca hc vin"
      headStyle={{ fontSize: "18px", paddingLeft: "10px", fontWeight: "bold" }}
      className="tw-mt-2"
    >
      {/* <div className="tw-flex"> */}
      {infoUser && (
        <div className="tw-flex tw-pb-10">
          <div className="tw-pr-3 tw-pt-[8px]">
            <Avatar size="large" src={infoUser?.image} />
          </div>
          <div className="tw-flex-1">
            <Form
              onFinish={onFinish}
              layout="horizontal"
              colon={false}
              form={form}
              initialValues={{
                number_stars: 1,
                content: "",
              }}
            >
              <Form.Item
                name="number_stars"
                label={<p style={{ fontWeight: "500" }}>nh gi ca bn:</p>}
                className="tw-mb-1"
              >
                <Rate
                  tooltips={["Rt t", "T", "Bnh Thng", "Tt", "Rt tt"]}
                  className="tw-ml-4"
                />
              </Form.Item>
              <Form.Item
                name="content"
                label={
                  <div>
                    <span style={{ fontWeight: "500" }}>Nhn xt: </span>
                    <span className="tw-text-sm tw-text-gray-400">
                      ( kin ca bn s thm thng tin cho nhiu hc vin tham
                      gia khc)
                    </span>
                  </div>
                }
                className="tw-block"
                rules={[
                  {
                    required: true,
                    message: "Hy nhp ni dung nh gi",
                  },
                ]}
              >
                <Input.TextArea rows={4} placeholder="Ni dung nh gi" />
              </Form.Item>
              <div className="tw-text-right">
                <button
                  className="theme-btn btn-style-three tw-border-0 tw-bg-black tw-p-2"
                  type="submit"
                >
                  <div className="tw-relative tw-z-[1] ">
                    <span
                      className="tw-normal-case
                 tw-text-white tw-px-2 tw-font-medium"
                    >
                      {loadingAddComment && (
                        <LoadingOutlined className="tw-mr-2" />
                      )}
                      Gi nh gi
                    </span>
                  </div>
                </button>
              </div>
            </Form>
          </div>
        </div>
      )}
      <div className="tw-font-semibold">Tt c nhn xt</div>
      <List
        pagination={comments_client?.lenght > 0}
        locale={{
          emptyText: (
            <Empty
              image={Empty.PRESENTED_IMAGE_SIMPLE}
              description={<span>Cha c nhn xt no</span>}
            />
          ),
        }}
        itemLayout="horizontal"
        dataSource={comments_client}
        renderItem={(item) => (
          <List.Item key={item.id}>
            <List.Item.Meta
              avatar={<Avatar size="large" src={item?.user_comment?.image} />}
              title={
                <>
                  <div className="tw-font-medium tw-capitalize">
                    {item?.user_comment?.name}
                  </div>
                  <div>
                    <Rate
                      defaultValue={item.number_stars}
                      disabled
                      className="tw-text-sm ant-rate-star-custom"
                    />
                  </div>
                </>
              }
              description={
                <div className="tw-text-gray-600">{item.content}</div>
              }
            />
          </List.Item>
        )}
      />
    </Card>
  );
};

export default CourseComment;
