import { LoadingOutlined, PlusOutlined } from "@ant-design/icons";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import { unwrapResult } from "@reduxjs/toolkit";
import {
  Button,
  Card,
  Col,
  Form,
  Input,
  message,
  notification,
  Row,
  Select,
  Upload,
} from "antd";
import Editor from "ckeditor5-custom-build/build/ckeditor";
import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router";
import ROUTER from "../../../../router/router";
import { Api } from "../../../../utils/Api";
import { EDITORCONFIGURATION, UPLOAD_API_URL } from "../../../../utils/Config";
import { addCourses } from "../CoursesSlice";
const CoursesAdd = () => {
  const { Option } = Select;
  useEffect(() => {
    const getData = async () => {
      const { data: getSpecialize } = await Api.get("/specialize-detail/pt");
      const { data: getLevel } = await Api.get("/customer_level");
      setSpecialize(getSpecialize.data);
      setLevel(getLevel.data);
    };

    getData();
  }, []);
  const [imageUrl, setImageUrl] = useState("");

  // New upload

  const [isUploading, setIsUploading] = useState(false);
  function getBase64(img, callback) {
    const reader = new FileReader();
    reader.addEventListener("load", () => callback(reader.result));
    reader.readAsDataURL(img);
  }

  function beforeUpload(file) {
    const isJpgOrPng = file.type === "image/jpeg" || file.type === "image/png";
    if (!isJpgOrPng) {
      message.error("Ch c th ti ln file JPEG v PNG!");
    }
    const isLt2M = file.size / 1024 / 1024 < 5;
    if (!isLt2M) {
      message.error("nh phi nh hn 5MB!");
    }
    return isJpgOrPng && isLt2M;
  }

  const handleUploadImage = (info) => {
    if (info.file.status === "uploading") {
      setIsUploading(true);
      return;
    }
    if (info.file.status === "done") {
      getBase64(info.file.originFileObj, (imageUrl) => {
        setImageUrl(info.file.response.url);
        setIsUploading(false);
      });
      message.success(`Ti ln nh ${info.file.originFileObj.name} thnh cng`);
    }
    if (info.file.status === "error") {
      setIsUploading(false);
      message.error(`${info.file.response.status}`);
    }
  };

  const uploadButton = (
    <div>
      {isUploading ? <LoadingOutlined /> : <PlusOutlined />}
      <div style={{ marginTop: 8 }}>Ti ln</div>
    </div>
  );

  // New upload

  const [specialize, setSpecialize] = useState([]);
  const [level, setLevel] = useState([]);
  const [description, setDescription] = useState("");
  const [content, setContent] = useState("");
  const dispatch = useDispatch();
  let history = useHistory();

  const render = () => {
    const onFinish = async (value) => {
      value.description = description;
      value.content = content;
      value.image = imageUrl;
      try {
        const resulDispatch = await dispatch(addCourses(value));
        unwrapResult(resulDispatch);
        history.push(ROUTER.PT.COURSES);
        setImageUrl("");
        notification.success({ message: `Thm thnh cng !!!` });
      } catch (error) {
        if (imageUrl === "") {
          notification.error({ message: `Cha chn nh` });
        } else {
          return notification.error({ message: `${error}` });
        }
      }
    };

    return (
      <>
        <Form layout="vertical" id="courses" onFinish={onFinish}>
          <Row gutter={24}>
            <Col span={24}>
              <Row gutter={24}>
                <Col span={12}>
                  <Form.Item
                    label="Tn kha hc"
                    name="name"
                    rules={[
                      {
                        required: true,
                        message: "Mi nhp tn kha hc",
                      },
                    ]}
                  >
                    <Input placeholder="Tn kha hc" />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    label="Trng thi hin th"
                    name="display"
                    rules={[
                      {
                        required: true,
                        message: "Mi chn trng thi hin th",
                      },
                    ]}
                  >
                    <Select defaultValue="Trng thi hin th">
                      <Option value="Active">Hin th</Option>
                      <Option value="Inactive">n</Option>
                    </Select>
                  </Form.Item>
                </Col>

                <Col span={12}>
                  <Form.Item
                    label="Gi"
                    name="price"
                    rules={[
                      {
                        required: true,
                        message: "Mi nhp gi",
                      },
                      {
                        pattern: /^(0|[1-9][0-9]*)$/,
                        message: "Vui lng nhp bng s",
                      },
                    ]}
                  >
                    <Input placeholder="Gi" />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    name="image"
                    label="nh"
                    rules={[
                      {
                        required: false,
                        message: "Mi ti ln nh",
                      },
                    ]}
                  >
                    <Upload
                      listType="picture-card"
                      className="avatar-uploader"
                      showUploadList={false}
                      action={UPLOAD_API_URL}
                      beforeUpload={beforeUpload}
                      onChange={handleUploadImage}
                    >
                      {imageUrl ? (
                        <img
                          src={imageUrl}
                          alt="avatar"
                          style={{ width: "100%", height: "100%" }}
                        />
                      ) : (
                        uploadButton
                      )}
                    </Upload>
                  </Form.Item>
                </Col>

                <Col span={12}>
                  <Form.Item
                    label=" Thi gian/bui (pht)"
                    name="time_a_lessons"
                    rules={[
                      {
                        required: true,
                        message: "Mi nhp thi gian/bui",
                      },
                      {
                        pattern: /^(0|[1-9][0-9]*)$/,
                        message: "Vui lng nhp bng s",
                      },
                    ]}
                  >
                    <Input placeholder="Thi gian/bui" />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    label="Tng s bui"
                    name="lessons"
                    rules={[
                      {
                        required: true,
                        message: "Mi nhp tng s bui",
                      },
                      {
                        pattern: /^(0|[1-9][0-9]*)$/,
                        message: "Vui lng nhp bng s",
                      },
                    ]}
                  >
                    <Input placeholder="Tng s bui" />
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    label="Cp "
                    name="customer_level_id"
                    rules={[
                      {
                        required: true,
                        message: "Mi nhp cp ",
                      },
                    ]}
                  >
                    <Select
                      className="select-custom"
                      placeholder="Mi nhp cp "
                    >
                      {level?.map((item, index) => {
                        return (
                          <Option key={index} value={item?.id}>
                            {item?.name}
                          </Option>
                        );
                      })}
                    </Select>
                  </Form.Item>
                </Col>

                <Col span={12}>
                  <Form.Item
                    label="Chuyn mn"
                    name="specialize_detail_id"
                    rules={[
                      {
                        required: true,
                        message: "Chuyn mn",
                      },
                    ]}
                  >
                    <Select
                      className="select-custom"
                      placeholder="Hy chn chuyn mn"
                    >
                      {specialize.map((item, index) => {
                        return (
                          <Option key={index} value={item?.id}>
                            {item?.specialize?.name}
                          </Option>
                        );
                      })}
                    </Select>
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item
                    label="M t"
                    name="description"
                    rules={[
                      {
                        required: true,
                        message: "Mi nhp m t",
                      },
                    ]}
                  >
                    <CKEditor
                      editor={Editor}
                      config={EDITORCONFIGURATION}
                      data=""
                      onChange={(event, editor) => {
                        const data = editor.getData();
                        setDescription(data);
                      }}
                    />
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <Form.Item
                    label="Ni dung"
                    name="content"
                    rules={[
                      {
                        required: true,
                        message: "Mi nhp ni dung",
                      },
                    ]}
                  >
                    <CKEditor
                      editor={Editor}
                      config={EDITORCONFIGURATION}
                      data=""
                      onChange={(event, editor) => {
                        const data = editor.getData();
                        setContent(data);
                      }}
                    />
                  </Form.Item>
                </Col>

                <div className="tw-text-center tw-flex-auto">
                  <Form.Item>
                    <Button type="primary" className="" htmlType="submit">
                      Thm mi
                    </Button>

                    <Button
                      className="ml-4"
                      type="primary"
                      danger
                      onClick={() => history.push(ROUTER.PT.COURSES)}
                    >
                      Thot
                    </Button>
                  </Form.Item>
                </div>
              </Row>
            </Col>
          </Row>
        </Form>
      </>
    );
  };

  return (
    <>
      <Card style={{ backgroundColor: "#ffffff", borderRadius: "8px" }}>
        {render()}
      </Card>
    </>
  );
};

export default CoursesAdd;
