import { Button, Descriptions, Modal, Table } from "antd";
import React, { useState } from "react";
import { useSelector } from "react-redux";
import { convertCurrency } from "../../../../utils";

const ListBill = () => {
  const { loading, data } = useSelector((state) => state.transactions);

  const [visible, setVisible] = useState(false);
  const [bill, setBill] = useState(null);
  const showModal = (value) => {
    setBill(value);
    setVisible(true);
  };

  const columns = [
    {
      title: "M VNPay",
      dataIndex: "code_vnp",
      key: "code_vnp",
      render: (text) => text,
    },
    {
      title: "Hc vin",
      dataIndex: "name",
      key: "name",
      render: (text) => text,
    },
    {
      title: "Ngn hng",
      dataIndex: "code_bank",
      key: "code_bank",
      render: (t, r, i) => t,
    },
    {
      title: "S tin",
      dataIndex: "money",
      key: "money",
      render: (money) => convertCurrency(money),
    },
    {
      title: "Ni dung",
      dataIndex: "note",
      key: "note",
      render: (text) => <>{text === "CoursePayment" ? "Thanh ton kha hc" : "Np tin"}</>,
    },
    {
      title: "Thi gian",
      dataIndex: "time",
      key: "time",
      width: 150,
      render: (time) => time,
    },
    {
      title: "Ha n",
      dataIndex: "",
      key: "",
      width: 120,
      render: (t, r, i) => (
        <>
          {r.bill !== null ? (
            <>
              <Button onClick={() => showModal(r.bill)}>Xem ha n</Button>

              {/*  Modal show ha n */}
              <Modal
                title="Thng tin ha n"
                onOk={() => setVisible(false)}
                onCancel={() => setVisible(false)}
                visible={visible}
                width={1000}
                cancelText="Thot"
                okButtonProps={{ hidden: true }}
              >
                <Descriptions title={`Ha n: #${bill?.id}`} bordered>
                  <Descriptions.Item label="M n" span={2}>
                    #{bill?.code_bill}
                  </Descriptions.Item>

                  <Descriptions.Item label="S tin">
                    {convertCurrency(bill?.money)}
                  </Descriptions.Item>
                  <Descriptions.Item label="Tn kha hc" span={3}>
                    {bill?.course?.name}
                  </Descriptions.Item>
                  <Descriptions.Item label="Thanh ton">
                    {bill?.status === "Wallet" ? "Bng v" : "Trc tip"}
                  </Descriptions.Item>
                  <Descriptions.Item label="Thi gian">{bill?.time}</Descriptions.Item>
                </Descriptions>
              </Modal>
            </>
          ) : (
            ""
          )}
        </>
      ),
    },
  ];
  return <Table loading={loading} columns={columns} dataSource={data} scroll={{ x: 768 }}></Table>;
};

export default ListBill;
